import React from 'react'
import { useSelector } from 'react-redux'

import { format } from 'date-fns'


// import { silences } from "../../app/dummyData";

const audioPosition = (time) => {
	// console.log(time)
	return format(time, '00:mm:ss.SSS')
}

let Bookmark = ({ time, highlight }) => {
	let defaultClass = "px-1 col-2 timemark"
	let leftClass = "px-1 col-2 timemark text-info"
	let bg = highlight == 1
		? `${defaultClass} bg-success`
		: highlight == 2 ? defaultClass
		: leftClass

	let copyToClipboard = (e) => {
		let container = e.target
		if (document.selection) {
			let range = document.body.createTextRange()
			range.moveToElementText(container)
			range.select().createTextRange()
			document.execCommand("copy")
		} else if (window.getSelection) {
			let range = document.createRange()
			range.selectNode(container)
			let selection = window.getSelection()
			selection.removeAllRanges()
			selection.addRange(range)
			document.execCommand("copy")
		}
	}

	// console.log("Bookmark", time)
	// console.log("Bookmark", time, audioPosition(time))
	return (
		<div className={bg} onClick={copyToClipboard} >{audioPosition(time)}</div>
	)
}

export function Silence() {
	const data = useSelector(state => state.lesson)
	let showSilences = data.showSilences
	let currentTime = data.currentTime
	let silences = data.silences || []
	const ref = React.createRef();

	// debugger
	let currentIdx = -1;
	for(let i=0; i<silences.length-1; i++) {
		let r1 = silences[i]
		let r2 = silences[i + 1]
		if (r1.end < currentTime && currentTime < r2.start) {
			currentIdx = i
			break;
		}
	}

	// debugger
	let content = silences.map((o, idx) => (
		<Bookmark key={idx} highlight={idx === currentIdx ? 1 : (o.end < currentTime) ? 0 : 2 } time={Number(o.end) * 1000} />
	))

	return (
			<div className="container-fluid1 row my-3 overflow-auto silence-container" ref={ref}>
				{ showSilences && content }
			</div>
	)
		// <div className="row my-3">
		// </div>
}
