			{
				"zh": "山本：看得懂，但是听不懂。\r\n看了演出能猜得出大概的意思来。",
				"py": "Shānběn: Kàn dé dǒng, dànshì tīng bù dǒng.\r\nKànle yǎnchū néng cāi dé chū dàgài de yìsi lái.",
				"vn": "Sơn Bản: Xem chắc là hiểu, nhưng mà nghe chắc là không hiểu.\r\nXem biểu diễn xong có thể đoán được ra ý nghĩa đại khái của nó.",
				"from": 28.418,
				"to": 0
			},
			{
				"zh": "玛丽：我也是，一点也听不懂演员唱的是什么，只是觉得很热闹。",
				"py": "Mǎlì: Wǒ yěshì, yīdiǎn yě tīng bù dǒng yǎnyuán chàng de shì shénme, zhǐshì juédé hěn rènào.",
				"vn": "Mã Lệ: tôi cũng thế, nghe chẳng hiểu tí nào diễn viên hát cái gì, chỉ cảm thấy rất vui nhộn.",
				"from": 38.178,
				"to": 0
			},
			{
				"zh": "山本：我觉得京剧唱得特别好听，武打动作也很精彩。\r\n我还喜欢京剧的脸谱，京剧用各种脸谱来表现人物的社会地位和性格，十分有趣。",
				"py": "Shānběn: Wǒ juédé jīngjù chàng dé tèbié hǎotīng, wǔdǎ dòngzuò yě hěn jīngcǎi.\r\nWǒ hái xǐhuān jīngjù de liǎnpǔ, jīngjù yòng gè zhǒng liǎnpǔ lái biǎoxiàn rénwù de shèhuì dìwèi hé xìnggé, shífēn yǒuqù.",
				"vn": "Sơn Bản: Tôi cảm thấy nghe hát kinh kịch rất hay, động tác múa võ cũng rất hay.\r\nTôi còn thích mặt vẽ của kinh kịch, kinh kịch thể hiện địa vị xã hội và tính cách nhân vật qua các mặt vẽ, vô cùng thú vị.",
				"from": 47.503,
				"to": 0
			},
			{
				"zh": "玛丽：京剧的服装也很美，我想买一套带回国去。",
				"py": "Mǎlì: Jīngjù de fúzhuāng yě hěn měi, wǒ xiǎng mǎi yī tào dài huíguó qù.",
				"vn": "Mã Lệ: Trang phục của kinh kịch cũng rất đẹp, tôi muốn mua một bộ mang về nước.",
				"from": 67.983,
				"to": 0
			},
			{
				"zh": "山本：你怎么这么喜欢京剧呢？",
				"py": "Shānběn: Nǐ zěnme zhème xǐhuān jīngjù ne?",
				"vn": "Sơn Bản: Sao bạn thích kinh kịch thế?",
				"from": 75.106,
				"to": 0
			},
			{
				"zh": "玛丽：我受中文老师的影响，他是一个京剧迷。\r\n我想，京剧是中国的传统艺术，要把汉语学好，也应该多了解一些中国文化。",
				"py": "Mǎlì: Wǒ shòu zhōngwén lǎoshī de yǐngxiǎng, tā shì yīgè jīngjù mí.\r\nWǒ xiǎng, jīngjù shì zhōngguó de chuántǒng yìshù, yào bǎ hànyǔ xuéhǎo, yě yīnggāi duō liǎo jiè yīxiē zhōngguó wénhuà.",
				"vn": "Mã Lệ: Tôi bị ảnh hưởng bởi thầy giáo tiếng Trung, thầy ấy là một người mê kinh kịch.\r\nTôi nghĩ là, kinh kịch là nghệ thuật truyền thống của Trung Quốc, muốn học tốt tiếng Trung cũng phải nên tìm hiểu thêm một số văn hóa Trung Hoa.",
				"from": 79.554,
				"to": 0
			},
			{
				"zh": "山本：要是有时间的话，自们一起去看一次，好吗？",
				"py": "Shānběn: Yàoshi yǒu shíjiān dehuà, zánmen yīqǐ qù kàn yīcì, hǎo ma?",
				"vn": "Sơn Bản: Nếu như có thời gian, chúng mình cùng nhau đi xem một lần, có được không?",
				"from": 97.14500000000001,
				"to": 0
			},
			{
				"zh": "玛丽：好啊。 ",
				"py": "Mǎlì: Hǎo a.",
				"vn": "Mã Lệ: Được.",
				"from": 103.206,
				"to": 0
			},
			{
				"zh": "（二）她有事，去不了",
				"py": "(Èr) tā yǒushì, qù bùliǎo",
				"vn": "Cô ấy có việc, khả năng không đi được.",
				"from": 107.58,
				"to": 0
			},
			{
				"zh": "昨天我给山本打电话，约她晚上一起去看京剧。",
				"py": "Zuótiān wǒ gěi shānběn dǎ diànhuà, yuē tā wǎnshàng yīqǐ qù kàn jīngjù.",
				"vn": "Hôm qua tôi gọi điện cho Sơn Bản, hẹn cô ấy buổi tối cùng nhau đi xem kinh kịch.",
				"from": 111.294,
				"to": 0
			},
			{
				"zh": "但是她说晚上有事，去不了。",
				"py": "Dànshì tā shuō wǎnshàng yǒushì, qù bùliǎo.",
				"vn": "Nhưng cô ấy nói buổi tối có việc, chắc là không đi được.",
				"from": 119.17699999999999,
				"to": 0
			},
			{
				"zh": "所以我们就决定今天晚上去。",
				"py": "Suǒyǐ wǒmen jiù juédìng jīntiān wǎnshàng qù.",
				"vn": "Cho nên chúng tôi mới quyết định tối nay đi.",
				"from": 123.991,
				"to": 0
			},
			{
				"zh": "我们是下午五点半出发的。",
				"py": "Wǒmen shì xiàwǔ wǔ diǎn bàn chūfā de.",
				"vn": "Năm giờ ba mươi chiều chúng tôi đi.",
				"from": 129.138,
				"to": 0
			},
			{
				"zh": "正是上下班时间，路上人多车也多。",
				"py": "Zhèng shì shàng xiàbān shíjiān, lùshàng rén duō chē yě duō.",
				"vn": "Đúng vào giờ tan tầm, trên đường đi người đông xe cũng đông.",
				"from": 133.304,
				"to": 0
			},
			{
				"zh": "公共汽车上不去，我们只好打的。",
				"py": "Gōnggòng qìchē shàng bù qù, wǒmen zhǐhǎo dǎ di.",
				"vn": "Xe buýt chắc là không lên được, chúng tôi đành phải đi tắc xi.",
				"from": 140.436,
				"to": 0
			},
			{
				"zh": "我担心买不到票，山本说，票好买，肯定买得到。",
				"py": "Wǒ dānxīn mǎi bù dào piào, shānběn shuō, piào hǎo mǎi, kěndìng mǎi dédào.",
				"vn": "Tôi lo không mua được vé, Sơn Bản nói, vé dễ mua lắm, Chắc chắn là mua được.",
				"from": 146.084,
				"to": 0
			},
			{
				"zh": "山本希望能买到前十排的。",
				"py": "Shānběn xīwàng néng mǎi dào qián shí pái de.",
				"vn": "Sơn Bản mong có thể mua được vé trước hàng ghế thứ mười.",
				"from": 155.214,
				"to": 0
			},
			{
				"zh": "因为她的眼睛不太好，坐得太远看不清楚。",
				"py": "Yīnwèi tā de yǎnjīng bù tài hǎo, zuò dé tài yuǎn kàn bù qīngchǔ.",
				"vn": "Bởi vì mắt cô ấy không tốt lắm, nếu như ngồi xa chắc là không nhìn được rõ.",
				"from": 159.54399999999998,
				"to": 0
			},
			{
				"zh": "但是，前十排的票都卖完了，没买到，我们买的是十二排的。",
				"py": "Dànshì, qián shí pái de piào dōu mài wánliǎo, méi mǎi dào, wǒmen mǎi de shì shí’èr pái de.",
				"vn": "Nhưng mà, vé mười hàng ghế trước đều đã bán sạch rồi, không mua được, chúng tôi mua vé là hàng ghế thứ mười hai.",
				"from": 166.935,
				"to": 0
			},
			{
				"zh": "这个剧场不大，估计坐不下一千人。",
				"py": "Zhège jùchǎng bù dà, gūjì zuò bùxià yīqiān rén.",
				"vn": "Rạp hát này không to, đoán là không chứa được một nghìn người.",
				"from": 177.728,
				"to": 0
			},
			{
				"zh": "离开演还有十多分钟，人差不多都坐满了。",
				"py": "Lí kāiyǎn hái yǒu shí duō fēnzhōng, rén chàbùduō dōu zuò mǎnle.",
				"vn": "Còn hơn mười phút nữa mới mở màn, khán giả gần như ngồi kín cả rồi.",
				"from": 184.061,
				"to": 0
			},
			{
				"zh": "我买了一张说明书。",
				"py": "Wǒ mǎile yī zhāng shuōmíngshū.",
				"vn": "Tôi mua một bản giới thiệu.",
				"from": 191.455,
				"to": 0
			},
			{
				"zh": "上面全是中文，没有英文，我看不懂。",
				"py": "Shàngmiàn quán shì zhōngwén, méiyǒu yīngwén, wǒ kàn bù dǒng.",
				"vn": "Bên trên toàn là tiếng Trung, không có tiếng Anh, tôi đọc không hiểu.",
				"from": 194.47,
				"to": 0
			},
			{
				"zh": "山本看了看说：“这是一个古代神话故事。",
				"py": "Shānběn kànle kàn shuō:“Zhè shì yīgè gǔdài shénhuà gùshì.",
				"vn": "Sơn Bản xem một chút rồi nói: “Đây là một câu chuyện cổ tích.",
				"from": 202.199,
				"to": 0
			},
			{
				"zh": "说的是天上一个仙女，很羡慕人间的生活，就偷偷来到人间，跟一个小伙子结了婚。",
				"py": "Shuō de shì tiānshàng yīgè xiānnǚ, hěn xiànmù rénjiān de shēnghuó, jiù tōutōu lái dào rénjiān, gēn yīgè xiǎo huǒ zǐ jiéle hūn.",
				"vn": "Chuyện kể rằng trên trời có một nàng tiên, thèm cuộc sống trần gian, bèn lén lút hạ phàm, rồi kết hôn với một chàng trai.",
				"from": 209.078,
				"to": 0
			},
			{
				"zh": "后边的内容我也看不懂。",
				"py": "Hòubian de nèiróng wǒ yě kàn bù dǒng.",
				"vn": "Nội dung đằng sau tôi đọc chắc cũng chẳng hiểu.",
				"from": 222.547,
				"to": 0
			},
			{
				"zh": "不过，不用担心，看不懂说明书，一定看得懂表演。”",
				"py": "Bùguò, bùyòng dānxīn, kàn bù dǒng shuōmíngshū, yīdìng kàn dé dǒng biǎoyǎn.”",
				"vn": "Có điều, không phải lo, đọc không hiểu bản giới thiệu, họ biểu diễn gì thì mình nhất định sẽ hiểu.”",
				"from": 226.80700000000002,
				"to": 0
			},
			{
				"zh": "看完以后，我大概看懂了这个故事。",
				"py": "Kàn wán yǐhòu, wǒ dàgài kàn dǒngle zhège gùshì.",
				"vn": "Sau khi xem xong, nói chung là tôi đã hiểu câu chuyện này.",
				"from": 235.797,
				"to": 0
			},
			{
				"zh": "我对山本说，以后周末，我们还来看吧，京剧真有意思。",
				"py": "Wǒ duì shānběn shuō, yǐhòu zhōumò, wǒmen hái lái kàn ba, jīngjù zhēn yǒuyìsi.",
				"vn": "Tôi nói với Sơn Bản, sau này cứ cuối tuần, chúng mình cứ đến đây xem nhé, kinh kịch rất là thú vị.",
				"from": 241.823,
				"to": 0
			},
			{
				"zh": "南辕北辙",
				"py": "Nányuánběizhé",
				"vn": "Đi lệch hướng rồi.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "这是中国古代的一个故事。",
				"py": "Zhè shì zhōngguó gǔdài de yīgè gùshì.",
				"vn": "Đây là một câu chuyện thời cổ đại.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "前边跑过来了一辆马车，车上坐着一个大冨翁，后面放着一个大箱子，前面还坐着一个赶车的。",
				"py": "Qiánbian pǎo guòláile yī liàng mǎchē, chē shàng zuòzhe yīgè dà fù wēng, hòumiàn fàngzhe yīgè dà xiāngzi, qiánmiàn hái zuòzhe yīgè gǎnchē de.",
				"vn": "Có một chiếc xe ngựa đang từ phía trước chạy đến, ở trên xe có một người rất giàu, phía trước có một cái rương rất to, phía trước còn có một người đánh xe ngựa.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "赶车的不停地赶着马，马车跑得飞快。",
				"py": "Gǎnchē de bù tíng de gǎnzhe mǎ, mǎchē pǎo dé fēikuài.",
				"vn": "Người phu xe đang không ngừng đánh xe ngựa, xe ngựa chạy rất nhanh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "路上走过来一位老人。",
				"py": "Lùshàng zǒu guòlái yī wèi lǎorén.",
				"vn": "Có một ông cụ đang từ trên đường đi tới.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "老人挑着水。",
				"py": "Lǎorén tiāozhe shuǐ.",
				"vn": "Ông cụ đang gánh nước.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "大富翁看见这位老人，就叫赶车的停下车，想向老人要点儿水喝。",
				"py": "Dà fùwēng kànjiàn zhè wèi lǎorén, jiù jiào gǎnchē de tíng xià chē, xiǎng xiàng lǎorén yàodiǎn er shuǐ hē.",
				"vn": "Đại phú ông nhìn thấy ông lão này, liền bảo người điều khiển xe ngựa dừng xe lại, muốn xin ông cụ một ít nước để uống.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "老人问他们要到什么地方去，富翁大声地说：“我们要到楚国去。”",
				"py": "Lǎorén wèn tāmen yào dào shénme dìfāng qù, fùwēng dàshēng de shuō:“Wǒmen yào dào chǔ guó qù.”",
				"vn": "Ông cụ hỏi họ muốn đi đâu, phú ông lớn tiếng nói rằng: “Chúng tôi muốn đến Sở quốc.”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "老人一听就笑了，他对富翁说：“你们走错了，楚国在南边，应该往南走，怎么往北走呢”",
				"py": "Lǎorén yī tīng jiù xiàole, tā duì fùwēng shuō:“Nǐmen zǒu cuòle, chǔ guó zài nánbian, yīnggāi wǎng nán zǒu, zěnme wǎng běi zǒu ne”",
				"vn": "Ông cụ nghe thấy vậy liền cười, ông ấy nói với phú ông rằng: “Các con đi nhầm hướng rồi, nước Sở ở phía Nam, phải nên đi về phía Nam, sao lại đi về phía Bắc chứ?”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "富翁说：“没关系，你看，我的马跑得很快。”",
				"py": "Fùwēng shuō:“Méiguānxì, nǐ kàn, wǒ de mǎ pǎo dé hěn kuài.”",
				"vn": "Phú ông nói： “Không sao, bố nhìn, ngựa của tôi phi rất nhanh.”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "老人又说：“你的马跑得再快，可是这不是去楚国的路啊。\r\n马跑得越快离楚国越远。”",
				"py": "Lǎorén yòu shuō:“Nǐ de mǎ pǎo dé zài kuài, kěshì zhè bùshì qù chǔ guó de lù a.\r\nMǎ pǎo dé yuè kuài lí chǔ guó yuè yuǎn.”",
				"vn": "Cụ ông lại nói: “Ngựa của con có chạy nhanh đến mấy đi đến nữa, nhưng đây đâu phải là đường đến nước Sở.\r\nNgựa chạy càng nhanh thì càng xa nước Sở.”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "“没问题。”大富翁指着车上的箱子说：“您看，这个大箱子里装着很多钱呢。”",
				"py": "“Méi wèntí.” Dà fùwēng zhǐzhe chē shàng de xiāngzi shuō:“Nín kàn, zhège dà xiāngzi lǐ zhuāngzhe hěnduō qián ne.”",
				"vn": "“Không vấn đề.” Đại phú ông chỉ vào cái rương trên xe rồi nói: “Bố nhìn này, trong cái rương to này có rất nhiều tiền.”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "“你有很多钱，可是走的方向不对，是到不了楚国的。”",
				"py": "“Nǐ yǒu hěnduō qián, kěshì zǒu de fāngxiàng bùduì, shì dào bùliǎo chǔ guó de.”",
				"vn": "“Con có rất nhiều tiền, nhưng mà hướng của con đi không có đúng, sẽ không bao giờ đến được nước Sở đâu.”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "大富翁又指着赶车的说：“你看我这个车夫，身强力壮，赶车的技术也特别好。”",
				"py": "Dà fùwēng yòu zhǐzhe gǎnchē de shuō:“Nǐ kàn wǒ zhège chēfū, shēn qiánglì zhuàng, gǎnchē de jìshù yě tèbié hǎo.”",
				"vn": "Đại phú ông lại chỉ vào người đánh xe ngựa nói: “Bố xem phu xe của con, cơ thể cường tráng, điều khiển xe ngựa cũng rất giỏi.”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "老人觉得这位先生简直不懂道理，就说：“你的车夫是不错。\r\n可是，楚国在南边，你非要他赶着车往北跑，怎么能到得了楚国呢？”",
				"py": "Lǎorén juédé zhè wèi xiānshēng jiǎnzhí bù dǒng dàolǐ, jiù shuō:“Nǐ de chēfū shì bùcuò.\r\nKěshì, chǔ guó zài nánbian, nǐ fēi yào tā gǎnzhe chē wǎng běi pǎo, zěnme néng dào déliǎo chǔ guó ne?”",
				"vn": "Ông cụ cảm thấy vị tiên sinh này thật sự không hiểu đạo lý, liền nói: “Phu xe của con rất tuyệt vời.\r\nNhưng mà, nước Sở ở miền Nam, con bắt người ta đánh xe về phía Bắc, thì làm sao mà đến nước Sở được chứ?”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "大富翁听了老人的话，不高兴地说：“我们已经走了好几百里路了，怎么能再往回走呢？”",
				"py": "Dà fùwēng tīngle lǎorén dehuà, bù gāoxìng de shuō:“Wǒmen yǐjīng zǒule hǎo jǐ bǎi lǐ lùle, zěnme néng zài wǎng huí zǒu ne?”",
				"vn": "Đại phú ông nghe ông cụ nói xong, rất không vui nói rằng: “Chúng tôi đã đi được mấy trăm dặm rồi, làm sao có thể đi quay đầu được lại chứ?”",
				"from": 0,
				"to": 0
			},
			{
				"zh": "说完他就让赶车的继续赶着车一直往北跑去，车越跑越快，一会儿就看不见了。",
				"py": "Shuō wán tā jiù ràng gǎnchē de jìxù gǎnzhe chē yīzhí wǎng běi pǎo qù, chē yuè pǎo yuè kuài, yīhuǐ’er jiù kàn bùjiànle.",
				"vn": "Nói xong ông ta liền bảo phu xe tiếp tục đánh xe chạy thẳng về phía Bắc, một lúc thì không thấy nữa.",
				"from": 0,
				"to": 0
			}
