				"vn": "Bành Nghệ Vân không chi nổi học phí mấy nghìn bảng Anh, nhất định phải cố gắng dành được học bổng, nếu không thì cô ấy không còn cách nào tiếp tục học tiếp nữa.",
				"from": 0,
				"to": 0
