				"py": "Mìshū xuānbù:\r\n“Huòdé ā jiā’ěr jiàoshòu bóshì yánjiūshēng zīgé de shì láizì zhōngguó de lúndūn dàxué xuéshēng péngyìyún xiǎojiě!”",
				"vn": "Thư kí nói:\r\n“Giành được tư cách nghiên cứu sinh tiến sĩ của giáo Archer là cô Bành Nghệ Vân học sinh trường đại học Luân Đôn đến từ Trung Quốc.",
