				"vn": "Giáo sư Archer đứng dậy, ông ấy đi đến trước mặt Bành Nghệ Vân, trước mặt mọi người nói với cô ấy rằng:\r\n“Con thấy chưa, con tranh luận với ta mất hai tiếng, nhưng mà tôi vẫn quyết định nhận con.",
