				"py": "Tā zhōngyú shíxiànle zìjǐ de yuànwàng, zuòwéi ā jiā’ěr jiàoshòu de bóshì yánjiūshēng, zǒu jìnle tā xiàngwǎng yǐ jiǔ de niújīn dàxué.",
				"vn": "Cuối cùng cô ấy đã thực hiện được ước mơ của mình, làm nghiên cứu sinh tiến sĩ của giáo sư Archer, rồi bước vào trường đại học Oxford mà cô ấy hằng mong bấy lâu nay.",
