				"vn": "Tôi vừa bước vào phòng tiếp khách nhỏ, có một người nước ngoài ngồi ở giữa ghế sofa liền đứng dậy, tôi nhận ra, ông ấy chính là Bối Khắc tiên sinh, trên trang quảng cáo tuyển dụng của công ty có ảnh của ông ấy.",
				"from": 0,
				"to": 0
