import React from 'react';
import ReactDOM from 'react-dom';
import "bootswatch/dist/journal/bootstrap.min.css";
import './index.css';
import App from './App';
import store from './app/store';
import { Provider } from 'react-redux';
import * as serviceWorker from './serviceWorker';

import { fetchLesson } from './features/lesson/lessonSlice'
// import dummyData from './app/dummyData'
import allLessons from './app/dummyLessons'

let lesson = allLessons.find(x => x.lesson == "30101");
// debugger
store.dispatch(fetchLesson({
	audio: lesson.audio,
	subtitles: lesson.subtitles,
	silences: lesson.silences
}));

// store.dispatch(fetchLesson(dummyData[dummyData.length-1]))

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
