import React, {useEffect} from 'react'
import { useSelector, useDispatch } from 'react-redux'
// import { timeupdate } from './audioSlice'
import { textbookList, lessonsList, booksList } from '../../app/dummyData'
import allLessons from '../../app/dummyLessons'
import { fetchLesson } from './lessonSlice'

export function Lesson() {
	// const data = useSelector(state => state.lesson)
	// console.log(data)
	const dispatch = useDispatch()

	let onSelectTextBook = e => {
		console.log(e.target.value)
	}

	let onSelectBook = e => {

	}

	let selectLesson = e => {
		console.log(e.target.value);
		let lesson = allLessons.find(x => x.lesson == e.target.value);

		dispatch(fetchLesson({
			audio: lesson.audio,
			subtitles: lesson.subtitles,
			silences: lesson.silences
		}));
	}


	return (
		<form className="row my-3">
			<div className="col-4">
				<select className="custom-select" onChange={onSelectTextBook}>
					{textbookList.map((e, idx) => (
						<option key={idx} value={e}>{e}</option>
					))}
				</select>
			</div>
			<div className="col-4">
				<select className="custom-select" onChange={onSelectBook}>
					{booksList.map((e, idx) => (
						<option key={idx} value={e.book}>{e.name}</option>
					))}
				</select>
			</div>
			<div className="col-4">
				{/*<select className="custom-select">*/}
				{/*	{lessonsList.map((e, idx) => (*/}
				{/*		<option key={idx} value={e}>{e}</option>*/}
				{/*	))}*/}
				{/*</select>*/}
				<select className="custom-select" onChange={selectLesson}>
					{lessonsList.map((e, idx) => (
						<option key={idx} value={[e.book, e.lesson_code.padStart(2, "0"), "01"].join("")}>Quyển {e.book} - Bài {e.lesson_code} - {e.lesson_name}</option>
					))}
				</select>
			</div>
		</form>
	)
}

