import React, {useEffect} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { timeupdate, updateOptions } from './lessonSlice'

export function Audio() {
	const dispatch = useDispatch()
	const data = useSelector(state => state.lesson)
	const startFrom = data.startFrom
	let autoplay = data.autoplay
	let showSilences = data.showSilences
	let showPinyin = data.showPinyin
	let showTiengViet = data.showTiengViet
	let autoScroll = data.autoScroll

	const audioRef = React.createRef();		// get DOM ref tới audio
	const optionsRef = React.createRef();

	let toggleOptions = (e) => {
		optionsRef.current.classList.toggle("panel-invisible");
	}

	let onUpdateOptions = () => {
		dispatch(updateOptions({autoplay, autoScroll, showSilences, showPinyin, showTiengViet}))
	}

	// tương đương componentDidMount
	useEffect(() => {
		audioRef.current.addEventListener("timeupdate", function(e){
			if(audioRef && audioRef.current && audioRef.current.currentTime != null) {
				dispatch(timeupdate(audioRef.current.currentTime))
			}
		});
	})

	useEffect(() => {
		audioRef.current.currentTime = startFrom
	}, [audioRef, startFrom])
	return (
		<>
			<div className="row my-2" >
				<div className="container d-flex bd-highlight1 align-items-center">
					<audio className="player w-100 pr-2"
						ref={audioRef}
						src={data.audio}
						autoPlay={autoplay}
						loop controls controlsList="nofullscreen nodownload noremoteplayback">
					</audio>
					<button className="btn btn-primary1 btn-outline-primary btn-sm align-self-center pl-2"
							data-toggle="collapse" data-target="#options" onClick={toggleOptions}>
						<i className="fa fa-bars"></i>
					</button>
				</div>
			</div>
			<div className="row my-2" >
				<div ref={optionsRef} className="container d-flex bd-highlight align-items-center panel-invisible ">
					<div className="custom-control custom-switch mr-3">
						<input type="checkbox" className="custom-control-input" checked={autoplay} id="customSwitch1"
							   onChange={(e) => {
								   autoplay = e.target.checked
								   onUpdateOptions()
							   }}
						/>
						<label className="custom-control-label" htmlFor="customSwitch1">Auto play</label>
					</div>
					<div className="custom-control custom-switch mr-3">
						<input type="checkbox" className="custom-control-input" checked={autoScroll} id="customSwitch2"
							   onChange={(e) => {
								   autoScroll = e.target.checked
								   onUpdateOptions()
							   }}
						/>
						<label className="custom-control-label" htmlFor="customSwitch2">Auto scroll</label>
					</div>
					<div className="custom-control custom-switch mr-3">
						<input type="checkbox" className="custom-control-input" id="customSwitch3" checked={showSilences}
							   onChange={(e) => {
								   showSilences = e.target.checked
								   onUpdateOptions()
							   }}
						/>
						<label className="custom-control-label" htmlFor="customSwitch3">Silences</label>
					</div>
					<div className="custom-control custom-switch mr-3">
						<input type="checkbox" className="custom-control-input" id="customSwitch4" checked={showPinyin}
							   onChange={(e) => {
								   showPinyin = e.target.checked
								   onUpdateOptions()
							   }}
						/>
						<label className="custom-control-label" htmlFor="customSwitch4">Pinyin</label>
					</div>
					<div className="custom-control custom-switch mr-3">
						<input type="checkbox" className="custom-control-input" id="customSwitch5" checked={showTiengViet}
							   onChange={(e) => {
								   showTiengViet = e.target.checked
								   onUpdateOptions()
							   }}
						/>
						<label className="custom-control-label" htmlFor="customSwitch5">Tiếng Việt</label>
					</div>
				</div>
			</div>
		</>
	)
}
