import React, {useEffect} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { moveTo } from './lessonSlice'

import { format } from 'date-fns'

const audioPosition = (time) => {
	return format(time, 'mm:ss.SSS')
}

const timeToSeconds = (sTime) => {
	let b = sTime.split(/[:.]/).map(Number);
	return b[3]/1000 + b[2] + b[1] * 60 + b[0] * 3600
}

let Subtitle = function({ paragraph, highlight, onClickTime, col, showPinyin, showTiengViet } ) {
	// let bg = highlight? "row zero-clipboard bg-success text-white" : "row zero-clipboard"
	let defaultClass = "row subtitle zero-clipboard"
	let bg = highlight ? `${defaultClass} bg-warning text-white1` : defaultClass

	let fnClick = (e) => {
		onClickTime(paragraph.from)
	}
	// debugger
	return (
		<div className={bg}>
			<div className={col}>{paragraph.zh}</div>
			{showPinyin && <div className={col}>{paragraph.py}</div>}
			{showTiengViet && <div className={col}>{paragraph.vn}</div>}
			{paragraph.from ==0? null:
				<div className="btn-clipboard border btn btn-outline-primary btn-small rounded-pill" onClick={fnClick}>
					{audioPosition(paragraph.from*1000)}
				</div>

			}
		</div>
	)
}
export function Subtitles() {
	// const dispatch = useDispatch()
	const data = useSelector(state => state.lesson)
	const subtitles = data.subtitles

	let dispatch = useDispatch()
	let onClickTime = (time) => {
		// console.log("onClickTime", time);
		dispatch(moveTo(time))
	}
	// debugger
	// console.log(data.currentTime)
	const currentParagraph = data.currentParagraph;
	const autoScroll = data.autoScroll
	const showPinyin = data.showPinyin
	const showTiengViet = data.showTiengViet

	let cnt = 1;
	if(showPinyin) cnt++;
	if(showTiengViet) cnt++;
	let col = `col-${12/cnt}`;

	const ref = React.createRef();
	let content = subtitles.map((sub, idx) => (
		<Subtitle key={idx} paragraph={sub} highlight={idx == currentParagraph} onClickTime={onClickTime}
			col={col} showTiengViet={showTiengViet} showPinyin={showPinyin}
		/>
	))

	useEffect(() => {
		if(autoScroll && currentParagraph >=0 ) {
			ref.current.childNodes[currentParagraph].scrollIntoView()
		}

	}, [currentParagraph])

	return (
		<div className="row my-3">
			<div className="container-fluid overflow-auto pb-1">
				<div className="row zero-clipboard bg-primary text-white py-1">
					<div className={col}>汉语</div>
					{showPinyin && <div className={col}>Pinyin</div>}
					{showTiengViet && <div className={col}>Tiếng Việt</div>}
				</div>
			</div>
			<div ref={ref} className="container-fluid overflow-auto subtitle-container">
				{content}
			</div>
		</div>
	)
}
