import { createSlice } from '@reduxjs/toolkit';



export const lessonSlice = createSlice({
	name: 'lesson',
	initialState: {
		audio: "",
		currentParagraph: -1,
		currentTime: 0,
		subtitles: [],
		silences: [],
		autoScroll: true,
		showSilences: false,
		showPinyin: true,
		showTiengViet: true,
		autoplay: true,
		startFrom: 0,	// seconds. e.g 1.25
	},
	reducers: {
		fetchLesson: (state, action) => {
			state.audio = `/mp3/${action.payload.audio}`
			state.subtitles = action.payload.subtitles
			state.silences = action.payload.silences
			state.currentParagraph = -1
			state.currentTime = 0
		},

		updateOptions: (state, action) => {
			let o = action.payload
			state.autoplay = o.autoplay
			state.autoScroll = o.autoScroll
			state.showSilences = o.showSilences
			state.showPinyin = o.showPinyin
			state.showTiengViet = o.showTiengViet
		},

		timeupdate: (state, action) => {
			// payload: audio's currentTime (seconds)
			// console.log("timeupdate", action.payload)
			state.currentTime = action.payload
			const currentTime = action.payload

			for(let idx=0; idx<state.subtitles.length-1; idx++) {
				let left = state.subtitles[idx].from
				let right = left;
				for(let j=idx+1; j<state.subtitles.length; j++) {
					if(state.subtitles[j].from !=0) {
						right = state.subtitles[j].from
						break
					}
				}
				// let right = state.subtitles[idx+1].from
				if(left <= currentTime && currentTime < right) {
					state.currentParagraph = idx;
					break;
				}
			}
		},
		moveTo: (state, action) => {
			// payload: start from time (seconds)
			state.startFrom = action.payload;
		}

	},
});

export const { fetchLesson, updateOptions, timeupdate, moveTo } = lessonSlice.actions;

export default lessonSlice.reducer;
